*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.main_container{
    height: 50px;
    background-color: #c0c0c0;
    /* box-shadow: 10px 10px 5px lightblue; */
}
.main_container img{
    height: 40px;
    width: 40px;
    float: left;
    margin-left: 30px;
}
.main_container p{
    float: left;
    color: rgb(115, 73, 73);
    margin-top: 10px;
    background-color: coral;
    padding: 2px 5px 5px 0px;
    border-radius: 5px;
    font-size: 20px;
    font-weight: 700;
    margin-left: 15px;
    cursor: pointer;
}
nav ul{
    display: flex;
    float: right;
    padding-right: 20px;
}
nav ul li{
    list-style: none;
    padding-right: 10px;
    padding-left: 10px;
    margin-top: 9px;
    padding-bottom: 3px;
    cursor: pointer;
    font-size: 20px;
    font-weight: 600;
    border-radius: 5px;
    color: rgb(94, 62, 62);
}

nav ul li:hover{
    color: rgb(75, 91, 46);
    background-color: rgb(193, 221, 222);
    transition: 0.17s ease-in-out;
}
.links{
    text-decoration: none;
}